header {
  background: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  height: 64px;
  margin: auto;
  padding: 18px 20px;
  display: block;
}

.header-margin {
  margin-left: auto;
}

.header-flex a {
  color: #111;
  text-decoration: none;
}

header ul {
  margin: 0;
  list-style: none;
  display: none;
}

#header-sign-button {
  display: none;
}

.header-nav-wrapper {
  margin-left: 0;
  display: flex;
}

.header-logo {
  align-items: center;
