.header-switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.header-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.header-slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  border-radius: 45px;
  padding: .5px;
  transition: all .4s;
  position: absolute;
  inset: 1.5px 0 0;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.header-slider:before {
  content: "";
  background-color: var(--background-light);
  border-radius: 50%;
  width: 17px;
  height: 17px;
  padding: .5px;
  transition: all .4s;
  position: absolute;
  transform: translateX(21px);
}

input:checked + .header-slider {
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input:checked + .header-slider:before {
  transform: translateX(1px);
}

.header-hamburger {
  stroke: var(--black-text-color);
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: end;
  width: 28px;
  height: 28px;
  padding: 0;
}

.header-log-in {
